  transform-style: preserve-3d;
}
.project-card {
  backface-visibility: hidden;
  transform-origin: center center;
}
@media (max-width: 768px) {
  .card-slider {
    height: 50vh;
  }
  .project-info {
    height: 30%;
  }
}
